.options-wrapper {
    align-items:center;
    display:flex;
    justify-content:center;
}

.button1 { 
    opacity: 0;
    width: 0;
    height: 0;
}

.button2 { 
    opacity: 0;
    width: 0;
    height: 0;
}

.options-wrapper figure {
    position: relative;
}

.option {
    cursor: pointer;
    padding: 10px;
    :hover {
        filter: opacity(75%);
    }
}
.option-text {
    position: absolute;
    color: white;
    font-weight: bolder;
    padding: 10px;
    bottom: 10px;
    left: 10px;
    z-index: 1000;
}
.grayscale {
    filter: grayscale(1);
    transition: 3ms;
}

.hide {
    display: none;
    transition: 3ms;
}

